<template>
  <div>
    <el-input placeholder="搜索一下,你就知道" icon="search" v-model="key"></el-input>
    <div>
      <el-row class="pad-bt05 box v-c">
        <el-col :span="16" class="box v-c">
          <h3>手机</h3>
          <p class="pad-lr05">筛选</p>
        </el-col>
        <el-col :span="8" class="tx-r">
          <router-link to="/filter">
            <el-button class="btn" type="warning">筛选</el-button>
          </router-link>
        </el-col>
      </el-row>
    </div>
    <div class="device_list" v-loading="loading"
         element-loading-text="拼命加载中">
      <el-row>
        <el-col :span="8" class="device_item"
                v-for="(val,index) in device_list"
        v-show="search(val)">
          <router-link :to="{name:'phoneDetails',params:{index:index}}">
            <div class="box h-c">
              <div class="device iphone7"></div>
            </div>
            <p>{{val.phoneVersion}}</p>
            <h4>{{val.osVersion.substring(1)}}</h4>
            <h5>{{val.owner}}/{{val.location}}/{{val.state==1?"正常":"异常"}}</h5>
          </router-link>
        </el-col>
      </el-row>
    </div>
    <div v-show="noResult" class="tx-c pad-t05">
      <p class="fz1-2 t-ff0">对不起,在您的筛选条件下暂无筛选结果</p>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import {mapState, mapMutations, mapActions} from 'vuex'
  export default{
    data(){
      return {
        noResult:false,
        key:"",
        device_list: [],
        loading: false
      }
    },
    created(){
      if(this.filterBol){
        let searchParams=this.handleObj(this.filters);
        this.getPhoneList(searchParams);
        return;
      }
      this.getPhoneList();
    },
    methods: {
      getPhoneList(params){
        if(params==undefined){
          params={};
        }
        this.loading = true;
        this.$http.jsonp("http://172.16.2.206:8080/QAdemoWebApp/phoneSearch.do", {
          jsonp: 'jsoncallback',
          params:params
        }).then((response) => {
          // 响应成功回调
          this.loading = false;
          //console.log(data.data.result);
          if(response.data.result.length!=0){
            this.device_list = response.data.result;
            this.$store.device_list = response.data.result;
          }else{
            this.noResult=true;
          }
        }, (response) => {
          // 响应错误回调
          this.loading = false;
        })
      },
      search(obj){
        var key1=this.keywords;
        var arr1=[];
        for(var i in obj){
          if(obj[i]){
            arr1.push(obj[i]);
          }
        }
        return arr1.some(function (item) {
          //搜索时不区分大小写
          return item.toString().toUpperCase().indexOf(key1.toUpperCase())!=-1;
        })
      },
      test(){
        this.$store.state.keywords=this.key;
      },
      handleObj(obj){
        let searchParams={};
        for(var key in obj){
          if(obj[key]!==""){
            searchParams[key]=obj[key];
          };
        }
        return searchParams;
      },
      ...mapActions([
        'incrementCount' // 映射 this.increment() 为 this.$store.dispatch('increment')
      ])
    },
    computed: mapState([
      // 映射 this.keywords 为 store.state.keywords
      'keywords',
      'filters',
      'filterBol'
    ])
  }
</script>



