<template>
  <div>
    <el-row class="pad-bt05 box v-c">
      <el-col :span="24">
        <router-link to="./">
          <el-button class="btn" type="danger">返回</el-button>
        </router-link>
      </el-col>
    </el-row>
    <div v-for="(p,key) in phoneData" v-loading="loading"
         element-loading-text="修改中,请稍后">
      <div class="device_main box v-c middle">
        <div class="device_img iphone7"></div>
        <h2 class="mar-t1">{{p.phoneVersion}}</h2>
        <span class="mar-t05">{{p.osCategory+' '+p.osVersion.substring(1)}}</span>
        <el-row class="box h-c mar-t05 pad-bt05">
          <el-col :span="14">
            <el-select v-model="p.owner" placeholder="请选择" @change="changeOwner(p.owner)">
              <el-option v-for="item in options"
                         v-if="item.label!=p.holder"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-col>
        </el-row>
        <span class="mar-t05">{{p.location}}/{{p.state==1?"正常":"异常"}}</span>
      </div>
      <div class="device_detail">
        <div class="bb-d tx-c">
          <h3>手机详情</h3>
        </div>
        <el-row class="detail_item bb-d">
          <el-col :span="8">基本参数</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">品牌</span>
            <span class="pad-b05 pad-t05">型号</span>
            <span class="pad-b05 pad-t05">具体型号</span>
            <span class="pad-b05 pad-t05">上市时间</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.brand}}</span>
            <span class="pad-b05 pad-t05">{{p.phoneVersion}}</span>
            <span class="pad-b05 pad-t05">{{p.phoneVersionDetails}}</span>
            <span class="pad-b05 pad-t05">{{p.buyTime}}年</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">系统信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">系统</span>
            <span class="pad-b05 pad-t05">系统版本号</span>
            <span class="pad-b05 pad-t05">定制系统</span>
            <span class="pad-b05 pad-t05">定制系统版本号</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.osCategory}}</span>
            <span class="pad-b05 pad-t05">{{p.osVersion.substring(1)}}</span>
            <span class="pad-b05 pad-t05">{{p.customizeOSName?p.customizeOSName:"--"}}</span>
            <span class="pad-b05 pad-t05">{{p.customizeOSVersion?p.customizeOSVersion:"--"}}</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">屏幕信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">尺寸（英寸）</span>
            <span class="pad-b05 pad-t05">分辨率</span>
            <span class="pad-b05 pad-t05">ppi</span>
            <span class="pad-b05 pad-t05">运行内存(G)</span>
            <span class="pad-t05">充电口</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.screenInches}}</span>
            <span class="pad-b05 pad-t05">{{p.screenResolution}}</span>
            <span class="pad-b05 pad-t05">326</span>
            <span class="pad-b05 pad-t05">{{p.ram}}</span>
            <span class="pad-t05">{{p.chargingPortType}}</span>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">使用信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">持有人</span>
            <span class="pad-b05 pad-t05">所在地</span>
            <span class="pad-b05 pad-t05">状态</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">{{p.owner}}</span>
            <span class="pad-b05 pad-t05">上海</span>
            <span class="pad-b05 pad-t05">正常</span>
          </el-col>
        </el-row>
        <el-row class="detail_item bb-d">
          <el-col :span="8">购买信息</el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">购买金额</span>
            <span class="pad-b05 pad-t05">购买时间</span>
          </el-col>
          <el-col :span="8" class="box middle">
            <span class="pad-b05">￥{{p.price}}</span>
            <span class="pad-b05 pad-t05">{{p.buyTime}}年</span>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
  import {mapState} from 'vuex'
  import '../../assets/css/deatil.css'
  export default{
    data(){
      return {
        loading:false,
        pindex: this.$route.params.index,
        phoneData: [],
        options: [{
          value: '李林',
          label: '李林'
        }, {
          value: '尹瑞',
          label: '尹瑞'
        }, {
          value: '李畅',
          label: '李畅'
        }, {
          value: '黄进',
          label: '黄进'
        }, {
          value: '童克方',
          label: '童克方'
        }, {
          value: '周兴',
          label: '周兴'
        }, {
          value: '范文理',
          label: '范文理'
        }]
      }
    },
    methods: {
      changeOwner(owner){
        this.loading=true;
        this.$http.jsonp("http://172.16.2.206:8080/QAdemoWebApp/phoneUpdateOwner.do", {
          //修改持有人参数
          params:{
            uuid:this.phoneData[0]["uuid"],
            owner:owner,
          },
          jsonp: 'jsoncallback'
        }).then(function (response) {
          this.loading=false;
          this.open();
        },function (response) {
          this.loading=false;
        })
      },
      open() {
        this.$message({
          message:"修改持有人成功",
          type: 'success',
          duration: 1000
        });
      }
    },
    created(){
      this.phoneData[0] = this.$store.device_list[this.pindex];
    }
  }
</script>



